.recentSearchesLocofy,
.sin {
  position: relative;
  letter-spacing: 0.04em;
}
.recentSearchesLocofy {
  align-self: stretch;
  text-transform: uppercase;
}
.sin {
  margin: 0;
  font-size: inherit;
  text-transform: capitalize;
  font-weight: 700;
  font-family: inherit;
  display: inline-block;
  width: 44.7px;
  flex-shrink: 0;
}
.durationIcon {
  position: relative;
  width: 117.4px;
  height: 24px;
  object-fit: cover;
}
.lax {
  margin: 0;
  position: relative;
  font-size: inherit;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  font-weight: 700;
  font-family: inherit;
  text-align: right;
  display: inline-block;
  width: 51.3px;
  flex-shrink: 0;
}
.toAndFrom {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.departOn7 {
  position: relative;
  font-size: var(--font-size-xl);
  letter-spacing: 0.04em;
  text-transform: capitalize;
  font-family: var(--font-gravis-personal-use);
  color: var(--color-black);
}
.flightMainContainer {
  flex: 1;
  border-radius: var(--br-5xs);
  border: 1px solid var(--color-whitesmoke);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: var(--padding-xl);
  gap: var(--gap-2xl);
}
.departOn9Container,
.dub {
  position: relative;
  letter-spacing: 0.04em;
  text-transform: capitalize;
}
.dub {
  display: inline-block;
  text-align: right;
  width: 51.3px;
  flex-shrink: 0;
}
.departOn9Container {
  font-size: var(--font-size-xl);
  color: var(--color-black);
  font-family: var(--font-saira-extracondensed);
}
.recentSearches {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-mini);
}
.hotelIcon {
  position: relative;
  width: 65.4px;
  height: 65.4px;
}
.hotel1 {
  position: relative;
  font-size: var(--components-button-large-size);
  letter-spacing: 0.04em;
  font-family: var(--components-button-large);
  color: var(--color-gray-100);
  text-align: left;
}
.hotel {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: var(--gap-5xs);
}
.attractionsIcon {
  position: relative;
  border-radius: var(--br-base);
  width: 65.4px;
  height: 65.4px;
}
.attractions1 {
  position: relative;
  font-size: var(--components-button-large-size);
  letter-spacing: 0.04em;
  font-family: var(--components-button-large);
  color: var(--color-gray-100);
  text-align: center;
}
.taxiIcon {
  position: relative;
  border-radius: var(--br-base);
  width: 70px;
  height: 65.4px;
}
.taxi1 {
  position: relative;
  font-size: var(--components-button-large-size);
  letter-spacing: 0.04em;
  font-family: var(--components-button-large);
  color: var(--color-gray-100);
  text-align: center;
  display: inline-block;
  width: 32.1px;
}
.tripMenus {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
}
.prepareMenu {
  justify-content: center;
  gap: var(--gap-xl);
  font-size: var(--font-size-lg);
  color: var(--color-lightgray);
}
.flightDetails,
.prepareMenu,
.upcomingFlightSection,
.upcomingFlightSection1 {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.flightDetails {
  justify-content: center;
  gap: var(--gap-17xl);
  font-size: var(--font-size-5xl);
  color: var(--color-cornflowerblue-200);
}
.upcomingFlightSection,
.upcomingFlightSection1 {
  justify-content: flex-start;
}
.upcomingFlightSection1 {
  gap: var(--gap-xl);
}
.upcomingFlightSection {
  text-align: left;
  font-size: var(--font-size-lg);
  color: var(--color-darkslategray-200);
  font-family: var(--components-button-large);
}
@media screen and (max-width: 1200px) {
  .flightMainContainer {
    width: 520px;
  }
}
@media screen and (max-width: 960px) {
  .flightMainContainer {
    width: 100%;
    display: block;
    margin-bottom: 30px;
    flex: unset;
    align-self: stretch;
  }
  .recentSearches {
    flex-direction: column;
  }
  .prepareMenu {
    width: 100%;
  }
  .flightDetails {
    flex-direction: column;
  }
  .upcomingFlightSection {
    display: block;
  }
}
@media screen and (max-width: 420px) {
  .hotel {
    padding-bottom: var(--padding-xl);
    box-sizing: border-box;
    width: 33.33%;
    text-align: center;
  }
  .tripMenus {
    flex-direction: row;
    text-align: center;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-left: -28px;
    margin-right: -28px;
  }
}
