.mostPopularDestinations,
.planYourNext {
  align-self: stretch;
  position: relative;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}
.mostPopularDestinations {
  margin: 0;
  font-size: var(--font-size-11xl);
  text-transform: capitalize;
  font-weight: 700;
  font-family: inherit;
  color: var(--color-darkslategray-200);
}
.titleContainer {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-7xs);
}
.viewAllDestinations {
  position: relative;
  font-size: var(--font-size-lg);
  letter-spacing: 0.04em;
  font-weight: 500;
  font-family: var(--components-button-large);
  color: var(--color-cornflowerblue-200);
  text-align: right;
  display: inline-block;
  width: 197.3px;
  flex-shrink: 0;
}
.arrowRightIcon {
  position: relative;
  width: 24px;
  height: 24px;
  overflow: hidden;
  flex-shrink: 0;
}
.destinationsTitles,
.viewAllTop {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
.viewAllTop {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: var(--primary-contrast);
  border-radius: var(--br-12xl);
  width: 231.4px;
  align-items: flex-start;
  gap: var(--gap-3xs);
}
.destinationsTitles {
  align-self: stretch;
  align-items: center;
}
.paris {
  flex: 1;
  position: relative;
  letter-spacing: 0.02em;
}
.div,
.from {
  position: relative;
  display: inline-block;
}
.from {
  letter-spacing: 0.04em;
  text-transform: uppercase;
  width: 52.5px;
}
.div {
  font-size: var(--font-size-19xl);
  line-height: 38px;
  font-family: var(--font-baloo-bhai);
  width: 90.9px;
}
.details {
  width: 90.9px;
  height: 59px;
  flex-direction: column;
  align-items: flex-end;
  padding: var(--padding-0) 0.000003814697265625px var(--padding-0)
    var(--padding-0);
  box-sizing: border-box;
  gap: var(--gap-11xs);
  text-align: right;
  font-size: var(--font-size-base);
}
.destinationDetails,
.details,
.pariscard {
  display: flex;
  justify-content: flex-start;
}
.destinationDetails {
  width: 312.4px;
  flex-direction: row;
  align-items: flex-start;
  padding: var(--padding-5xs) var(--padding-xs);
  box-sizing: border-box;
}
.pariscard {
  border-radius: var(--br-3xs);
  width: 312.5px;
  height: 182px;
  overflow: hidden;
  flex-shrink: 0;
  flex-direction: column;
  align-items: center;
  background-image: url(/public/pariscard@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
}
.div1 {
  position: relative;
  font-size: var(--font-size-19xl);
  line-height: 38px;
  font-family: var(--font-baloo-bhai);
}
.details1,
.greececard,
.norwaycard {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.details1 {
  width: 99.9px;
  height: 59px;
  align-items: flex-end;
  padding: var(--padding-0) 0.000003814697265625px var(--padding-0)
    var(--padding-0);
  box-sizing: border-box;
  gap: var(--gap-11xs);
  text-align: right;
  font-size: var(--font-size-base);
}
.greececard,
.norwaycard {
  border-radius: var(--br-3xs);
  width: 312.5px;
  height: 182px;
  overflow: hidden;
  flex-shrink: 0;
  align-items: center;
  background-image: url(/public/greececard@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  cursor: pointer;
}
.norwaycard {
  background-image: url(/public/norwaycard@3x.png);
}
.div3 {
  position: relative;
  font-size: var(--font-size-19xl);
  line-height: 38px;
  font-family: var(--font-baloo-bhai);
  display: inline-block;
  width: 100.6px;
}
.details3 {
  width: 100.6px;
  height: 59px;
  flex-direction: column;
  align-items: flex-end;
  padding: var(--padding-0) 0.000003814697265625px var(--padding-0)
    var(--padding-0);
  box-sizing: border-box;
  gap: var(--gap-11xs);
  text-align: right;
  font-size: var(--font-size-base);
}
.cardsContainer,
.details3,
.tuscanycard {
  display: flex;
  justify-content: flex-start;
}
.tuscanycard {
  border-radius: var(--br-3xs);
  width: 312.5px;
  height: 182px;
  overflow: hidden;
  flex-shrink: 0;
  flex-direction: column;
  align-items: center;
  background-image: url(/public/tuscanycard@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
}
.cardsContainer {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-start;
  gap: var(--gap-3xs);
  margin-left: -16px;
  margin-right: -16px;
  font-size: var(--font-size-xl);
  color: var(--primary-contrast);
}
.viewAllDestinations1 {
  position: relative;
  font-size: var(--font-size-lg);
  letter-spacing: 0.04em;
  font-weight: 500;
  font-family: var(--components-button-large);
  color: var(--color-cornflowerblue-200);
  text-align: right;
  display: inline-block;
  width: 197.3px;
  flex-shrink: 0;
}
.popDestinationsMain,
.viewAllBottom {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.viewAllBottom {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: var(--primary-contrast);
  border-radius: var(--br-12xl);
  width: 231.4px;
  flex-direction: row;
  gap: var(--gap-3xs);
}
.popDestinationsMain {
  align-self: stretch;
  flex-direction: column;
  gap: var(--gap-5xl);
  text-align: left;
  font-size: var(--font-size-lg);
  color: var(--color-cornflowerblue-200);
  font-family: var(--components-button-large);
}
@media screen and (max-width: 960px) {
  .viewAllTop {
    display: none;
  }
  .cardsContainer {
    flex-wrap: wrap;
    margin: -16px;
  }
  .viewAllDestinations1 {
    width: auto;
  }
  .viewAllBottom {
    display: flex;
    margin-top: 16px;
  }
}
@media screen and (max-width: 420px) {
  .mostPopularDestinations {
    font-size: var(--font-size-3xl);
  }
  .titleContainer {
    flex: unset;
    align-self: stretch;
  }
  .destinationsTitles {
    flex-direction: column;
    align-self: flex-start;
  }
}
