.bannerBackground {
  position: relative;
  width: 1440px;
  height: 640px;
  object-fit: cover;
}
.findTheBest,
.letsLocofyExplore {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-weight: 400;
}
.letsLocofyExplore {
  font-size: inherit;
  line-height: 67px;
  font-family: inherit;
}
.findTheBest {
  font-size: var(--font-size-5xl);
  line-height: 32px;
  font-family: var(--components-button-large);
}
.title {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-8xs);
}
.searchFlights {
  flex: 1;
  position: relative;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}
.flightType {
  width: 216.5px;
  height: 38px;
  gap: var(--gap-xl);
}
.flightOptions,
.flightType,
.formTitleGroup {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.formTitleGroup {
  align-self: stretch;
  overflow: hidden;
  padding: var(--padding-8xs);
  gap: var(--gap-8xs);
}
.departureField {
  align-self: stretch;
  flex: 1;
}
.inputsRow {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-8xs);
  gap: var(--gap-3xs);
}
.button {
  position: relative;
  font-size: var(--components-button-large-size);
  letter-spacing: 0.46px;
  line-height: 26px;
  text-transform: uppercase;
  font-weight: 500;
  font-family: var(--components-button-large);
  color: var(--primary-contrast);
  text-align: center;
  display: inline-block;
  width: 147px;
}
.buttonGroup,
.searchFlightsButton {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.searchFlightsButton {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: var(--color-orange);
  border-radius: var(--br-9xs);
  width: 164px;
  height: 56px;
  overflow: hidden;
  flex-shrink: 0;
}
.buttonGroup {
  padding: var(--padding-8xs);
}
.formInputsRow,
.searchForm {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.searchForm {
  border-radius: var(--br-3xs);
  background-color: var(--primary-contrast);
  flex-direction: column;
  padding: var(--padding-xl);
  gap: var(--gap-9xs);
  text-align: left;
  font-size: var(--font-size-xl);
  color: var(--color-darkslategray-200);
  font-family: var(--components-button-large);
}
.searchContainer,
.searchSection {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.searchContainer {
  width: 100%;
  height: 640px;
  justify-content: center;
  padding: var(--padding-0) var(--padding-121xl);
  box-sizing: border-box;
  gap: var(--gap-24xl);
  margin-top: -639px;
}
.searchSection {
  background: linear-gradient(78.78deg, #114f8b 5.2%, #2a9ad7);
  width: 1440px;
  height: 639.9px;
  justify-content: flex-start;
  text-align: center;
  font-size: var(--font-size-42xl);
  color: var(--primary-contrast);
  font-family: var(--font-baloo-bhai);
}
@media screen and (max-width: 960px) {
  .inputsRow {
    width: 100%;
    flex: unset;
    align-self: stretch;
  }
  .buttonGroup {
    width: 100%;
    text-align: left;
  }
  .formInputsRow,
  .searchForm {
    flex-direction: column;
  }
  .searchContainer {
    padding-left: var(--padding-11xl);
    padding-right: var(--padding-11xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 700px) {
  .searchFlightsButton {
    align-self: stretch;
    width: auto;
  }
}
@media screen and (max-width: 420px) {
  .letsLocofyExplore {
    font-size: var(--font-size-23xl);
    line-height: 48px;
  }
  .searchFlights {
    width: 100%;
    padding-bottom: var(--padding-3xs);
    margin-bottom: 6px;
    border-bottom: 1px;
    border-bottom-style: solid;
    border-bottom-color: #eaeaea;
    flex: unset;
    align-self: stretch;
  }
  .flightType {
    width: 100% !important;
  }
  .flightOptions {
    width: 100%;
  }
  .formTitleGroup {
    flex-direction: column;
    align-items: flex-start;
  }
  .departureField {
    flex: unset;
    align-self: stretch;
  }
  .inputsRow {
    flex-direction: column;
  }
  .searchFlightsButton {
    width: auto;
    align-self: unset;
  }
  .searchForm {
    margin-top: 20px;
  }
}
