.heroSection {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.recommendedHolidays {
  flex: 1;
  position: relative;
  letter-spacing: 0.04em;
  text-transform: capitalize;
}
.viewAllHolidays {
  position: relative;
  font-size: var(--font-size-lg);
  letter-spacing: 0.04em;
  font-weight: 500;
  font-family: var(--components-button-large);
  color: var(--color-cornflowerblue-200);
  text-align: right;
  display: inline-block;
  width: 165.9px;
  flex-shrink: 0;
}
.arrowRightIcon {
  position: relative;
  width: 24px;
  height: 24px;
  overflow: hidden;
  flex-shrink: 0;
}
.recCardsContainer,
.recTitle,
.viewAllHolidaysParent {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
.viewAllHolidaysParent {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: var(--primary-contrast);
  border-radius: var(--br-12xl);
  width: 199.9px;
  align-items: flex-start;
  gap: var(--gap-3xs);
}
.recCardsContainer,
.recTitle {
  align-self: stretch;
  align-items: center;
}
.recCardsContainer {
  align-items: flex-start;
  gap: var(--gap-3xs);
  margin-left: -16px;
  margin-right: -16px;
}
.viewAllHolidays1 {
  position: relative;
  letter-spacing: 0.04em;
  font-weight: 500;
  display: inline-block;
  width: 165.9px;
  flex-shrink: 0;
}
.viewAllBottom {
  border-radius: var(--br-12xl);
  background-color: var(--primary-contrast);
  width: 199.9px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-3xs);
  text-align: right;
  font-size: var(--font-size-lg);
  color: var(--color-cornflowerblue-200);
}
.recommendedHolidaysContainer {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: var(--gap-5xl);
}
.homeContents {
  width: 100%;
  align-items: center;
  padding: var(--padding-0) var(--padding-61xl);
  box-sizing: border-box;
  gap: var(--gap-31xl);
  max-width: 1280px;
  text-align: left;
  font-size: var(--font-size-11xl);
  color: var(--color-darkslategray-200);
  font-family: var(--components-button-large);
}
.footerSection,
.homeContents,
.homepage {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.footerSection {
  align-self: stretch;
  align-items: flex-start;
}
.homepage {
  position: relative;
  background-color: var(--primary-contrast);
  width: 100%;
  overflow: hidden;
  align-items: center;
  gap: var(--gap-31xl);
}
.homepage:hover {
  background: linear-gradient(#fff, #fff), #87550a;
}
@media screen and (max-width: 960px) {
  .viewAllHolidaysParent {
    display: none;
  }
  .recCardsContainer {
    margin-left: -16px;
    margin-right: -16px;
    flex-wrap: wrap;
  }
  .viewAllHolidays1 {
    width: auto;
  }
  .viewAllBottom {
    display: flex;
    mrgin-top: 16px;
  }
  .homeContents {
    padding-left: var(--padding-11xl);
    padding-right: var(--padding-11xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 420px) {
  .recommendedHolidays {
    font-size: var(--font-size-3xl);
  }
  .homepage {
    width: 100%;
  }
  .homepage:hover {
    background-color: var(--color-orange);
  }
}
