}
.popularStays {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  gap: var(--gap-xl);
  text-align: left;
  font-size: var(--font-size-11xl);
  color: var(--color-darkslategray-200);
  font-family: var(--components-button-large);
}
@media screen and (max-width: 1200px) {
  .hotelCard,
  .hotelCard2,
  .hotelCard3 {
    width: 25% !important;
  }
  .hotelCards {
    padding-right: var(--padding-21xl);
    box-sizing: border-box;
    margin-left: -12px;
    margin-right: -50px;
  }
}
@media screen and (max-width: 960px) {
  .viewAllStaysButton {
    display: none;
  }
  .hotelCard,
  .hotelCard2 {
    width: 100%;
  }
  .hotelCard3 {
    width: 33.33% !important;
  }
  .hotelCards {
    margin-right: -50px;
    margin-left: -12px;
  }
  .viewAllStays1 {
    width: auto;
  }
  .mobileViewAllStays {
    display: flex;
    margin-top: 16px;
  }
}
@media screen and (max-width: 420px) {
  .popularStays1 {
    font-size: var(--font-size-3xl);
  }
  .matterhornSuites {
    font-size: var(--font-size-xl);
  }
  .moreDetailsButton {
    width: 100% !important;
  }
  .hotelCard,
  .hotelCard2 {
    flex: unset;
    align-self: stretch;
  }
  .hotelCard {
    width: 100% !important;
    margin-bottom: 20px;
  }
  .hotelCard2 {
    display: flex;
  }
  .hotelCard2,
  .hotelCard3,
  .moreDetailsButton3 {
    width: 100% !important;
  }
  .hotelCard3 {
    display: flex;
    flex: unset;
    align-self: stretch;
  }
  .hotelCards {
    flex-direction: column;
    padding-right: var(--padding-0);
    box-sizing: border-box;
    margin-right: -12px;
    margin-left: -12px;
  }
}
